// pages / dashboard / facility / manage-facility

// Modules
import React from "react";
import {connect} from 'react-redux';
import Helmet from 'react-helmet';
import Link, {navigateTo} from "gatsby-link";

// Styles
// import styles from '../../../style/preloader.module.css';

// Components
// import ConversationSidebar from '../../../components/dashboard/conversation/ConversationSidebar';
// import ChatMessage from '../../../components/dashboard/conversation/ChatMessage';


class ManageFacility extends React.Component {
    componentDidMount() {
        $('.manage-facility-table').DataTable({
            pageLength: 10,
            responsive: true,
            dom: '<"html5buttons"B>lTfgitp',
            buttons: [
                {
                    extend: 'copy'
                },
                {
                    extend: 'csv'
                },
                {
                    extend: 'excel',
                    title: 'ExampleFile'
                },
                {
                    extend: 'pdf',
                    title: 'ExampleFile'
                },

                {
                    extend: 'print',
                    customize: function(win) {
                        $(win.document.body).addClass('white-bg');
                        $(win.document.body).css('font-size', '10px');

                        $(win.document.body).find('table')
                            .addClass('compact')
                            .css('font-size', 'inherit');
                    }
                }
            ]
        });

        $('.clockpicker').clockpicker({
            placement: 'bottom',
            'default': 'now',
            donetext: 'Done'
        });
    }

    addNewFacility() {
        $('#modalNewFacility').modal('show');
    }

    editFacility() {
        $('#modalEditFacility').modal('show');
    }

    deleteConfirmFacility() {
        $('#modalDeleteFacility').modal('show');
    }

    render() {
        if (this.props.user.loggedIn) {

            return (
              <span>

                    {/* Start of Helmet */}
                    <Helmet title="Facility - manage facility" meta={[
                        {
                            name: 'description',
                            content: 'Facility - manage facility'
                        }, {
                            name: 'keywords',
                            content: 'Facility'
                        }
                    ]}/>
                    {/* End of Helmet */}


                    {/* Start of Heading */}
                    <div className="row wrapper border-bottom white-bg page-heading">
                        <div className="col-lg-8">
                            <h2>Manage Facility</h2>
                            <ol className="breadcrumb">
                                <li>
                                    <Link to="/dashboard">Home</Link>
                                </li>
                                <li className="active">
                                    <strong>Manage Facility</strong>
                                </li>
                            </ol>
                        </div>
                    </div>
                    {/* End of Heading */}



                    {/* Start of Modal */}
                    <div className="modal inmodal" id="modalNewFacility" tabIndex="-1" role="dialog"  aria-hidden="true">
                        <div className="modal-dialog">
                            <div className="modal-content animated fadeIn">
                                <div className="modal-header">
                                    <button type="button" className="close" data-dismiss="modal"><span aria-hidden="true">&times;</span><span className="sr-only">Close</span></button>
                                    <i className="fa fa-building-o modal-icon"></i>
                                    <h4 className="modal-title">Add New Facility</h4>
                                    <small>Add new facility in your property to allow residents to utilize them.</small>
                                </div>
                                <div className="modal-body">
                                    <form className="form-horizontal">
                                        <div className="form-group">
                                            <label className="col-lg-2 control-label" htmlFor="firstName">Name *</label>
                                            <div className="col-lg-10">
                                                <input type="text" placeholder="Facility Name" id="firstName" className="form-control" required />
                                            </div>
                                        </div>
                                        <div className="form-group">
                                            <label className="col-lg-2 control-label" htmlFor="lastName">Starting time *</label>
                                            <div className="col-lg-10">

                                                <div className="input-group clockpicker" data-autoclose="true">
                                                    <input type="text" className="form-control" placeholder="Please choose allowed start time" required />
                                                    <span className="input-group-addon">
                                                        <span className="fa fa-clock-o"></span>
                                                    </span>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="form-group">
                                            <label className="col-lg-2 control-label" htmlFor="lastName">Ending time *</label>
                                            <div className="col-lg-10">

                                                <div className="input-group clockpicker" data-autoclose="true">
                                                    <input type="text" className="form-control" placeholder="Please choose allowed end time" required />
                                                    <span className="input-group-addon">
                                                        <span className="fa fa-clock-o"></span>
                                                    </span>
                                                </div>
                                            </div>
                                        </div>

                                        <small>(*) fields are required to be filled in</small>
                                    </form>
                                </div>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-white" data-dismiss="modal">Close</button>
                                    <button type="button" className="btn btn-primary">Add Facility</button>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="modal inmodal" id="modalEditFacility" tabIndex="-1" role="dialog"  aria-hidden="true">
                        <div className="modal-dialog">
                            <div className="modal-content animated fadeIn">
                                <div className="modal-header">
                                    <button type="button" className="close" data-dismiss="modal"><span aria-hidden="true">&times;</span><span className="sr-only">Close</span></button>
                                    <i className="fa fa-building-o modal-icon"></i>
                                    <h4 className="modal-title">Multipurpose Hall</h4>
                                </div>
                                <div className="modal-body">
                                    <form className="form-horizontal">
                                        <div className="form-group">
                                            <label className="col-lg-2 control-label" htmlFor="firstName">Name *</label>
                                            <div className="col-lg-10">
                                                <input type="text" placeholder="Facility Name" id="firstName" className="form-control" defaultValue="Multipurpose Hall" required />
                                            </div>
                                        </div>
                                        <div className="form-group">
                                            <label className="col-lg-2 control-label" htmlFor="lastName">Starting time *</label>
                                            <div className="col-lg-10">

                                                <div className="input-group clockpicker" data-autoclose="true">
                                                    <input type="text" className="form-control" placeholder="Please choose allowed start time" defaultValue="09:00" required />
                                                    <span className="input-group-addon">
                                                        <span className="fa fa-clock-o"></span>
                                                    </span>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="form-group">
                                            <label className="col-lg-2 control-label" htmlFor="lastName">Ending time *</label>
                                            <div className="col-lg-10">

                                                <div className="input-group clockpicker" data-autoclose="true">
                                                    <input type="text" className="form-control" placeholder="Please choose allowed end time" defaultValue="22:00" required />
                                                    <span className="input-group-addon">
                                                        <span className="fa fa-clock-o"></span>
                                                    </span>
                                                </div>
                                            </div>
                                        </div>

                                        <small>(*) fields are required to be filled in</small>
                                    </form>
                                </div>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-white" data-dismiss="modal">Close</button>
                                    <button type="button" className="btn btn-primary">Save Changes</button>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="modal inmodal" id="modalDeleteFacility" tabIndex="-1" role="dialog"  aria-hidden="true">
                        <div className="modal-dialog">
                            <div className="modal-content animated bounce">
                                <div className="modal-header">
                                    <button type="button" className="close" data-dismiss="modal"><span aria-hidden="true">&times;</span><span className="sr-only">Close</span></button>
                                    <i className="fa fa-exclamation-triangle modal-icon"></i>
                                    <h4 className="modal-title">Are you sure?</h4>
                                    This action is irreversible!
                                </div>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-white" data-dismiss="modal">Close</button>
                                    <button type="button" className="btn btn-danger">Proceed</button>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* End of Modal */}


                    {/* Start of Content */}
                    <div className="wrapper wrapper-content animated fadeInDown">
                        <div className="row">
                            <div className="col-lg-8 col-hcenter">
                                <div className="ibox float-e-margins">
                                    <div className="ibox-title">
                                        <h5>Facility Table</h5>
                                        <div className="ibox-tools">
                                            <a className="collapse-link">
                                                    <i className="fa fa-chevron-up"></i>
                                                </a>
                                            <a className="dropdown-toggle" data-toggle="dropdown" href="#">
                                                    <i className="fa fa-wrench"></i>
                                                </a>
                                            <ul className="dropdown-menu dropdown-user">
                                                <li><a href="#">Config option 1</a>
                                                </li>
                                                <li><a href="#">Config option 2</a>
                                                </li>
                                            </ul>
                                            <a className="close-link">
                                                    <i className="fa fa-times"></i>
                                                </a>
                                        </div>
                                    </div>
                                    <div className="ibox-content">
                                        <button type="button" className="btn btn-w-m btn-primary pull-right" onClick={this.addNewFacility.bind(this)}>
                                            Add New Facility
                                        </button>
                                        <div className="clearfix space-15"></div>
                                        <div className="table-responsive">
                                            <table className="table table-striped table-bordered table-hover manage-facility-table">
                                                <thead>
                                                    <tr>
                                                        <th>Facility Name</th>
                                                        <th>Allowed Booking Time</th>
                                                        <th>Edit Facility</th>
                                                        <th>Delete Facility</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr className="table-row-vertical-align-middle">
                                                        <td>
                                                            Gym Room
                                                        </td>
                                                        <td>
                                                            8:00 am to 10:00 pm
                                                        </td>
                                                        <td>
                                                            <a href="#" onClick={this.editFacility.bind(this)}>Edit</a>
                                                        </td>
                                                        <td>
                                                            <a href="#" onClick={this.deleteConfirmFacility.bind(this)}>Delete</a>
                                                        </td>
                                                    </tr>
                                                    <tr className="table-row-vertical-align-middle">
                                                        <td>
                                                            Multipurpose Hall
                                                        </td>
                                                        <td>
                                                            8:00 am to 10:00 pm
                                                        </td>
                                                        <td>
                                                            <a href="#" onClick={this.editFacility.bind(this)}>Edit</a>
                                                        </td>
                                                        <td>
                                                            <a href="#" onClick={this.deleteConfirmFacility.bind(this)}>Delete</a>
                                                        </td>
                                                    </tr>
                                                    <tr className="table-row-vertical-align-middle">
                                                        <td>
                                                            Swimming Pool
                                                        </td>
                                                        <td>
                                                            8:00 am to 10:00 pm
                                                        </td>
                                                        <td>
                                                            <a href="#" onClick={this.editFacility.bind(this)}>Edit</a>
                                                        </td>
                                                        <td>
                                                            <a href="#" onClick={this.deleteConfirmFacility.bind(this)}>Delete</a>
                                                        </td>
                                                    </tr>
                                                </tbody>
                                                {/*
                                                <tfoot>
                                                    <tr>
                                                        <th>ID</th>
                                                        <th>Transaction Date</th>
                                                        <th>Bill Type</th>
                                                        <th>Payment Method</th>
                                                        <th>Total Amount (RM)</th>
                                                        <th>Payment Status</th>
                                                        <th>View Payment</th>
                                                    </tr>
                                                </tfoot>
                                                */}
                                            </table>
                                        </div>

                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* End of Content */}

                </span>
            );
        } else {
            return null;
        }
    }
};

const mapStateToProps = (state) => {
    return {user: state.users.user};
}

const mapDispatchToProps = (dispatch) => {
    return {};
}

export default connect(mapStateToProps, mapDispatchToProps())(ManageFacility);



